/*! 我的消息记录 css */
.my-info {
  padding: @30px;
  min-height: 100%;
  background-color: #f7f8f9;
  .item {
    background-color: #fff;
    padding: 0 @30px 0 @20px;
    margin-bottom: @20px;
    .icon {
      width: @30px;
      height: @30px;
      margin-right: @8px;
    }
    .title {
      line-height: 1;
      height: @100px;
      border-bottom: 1px solid #ddd;
      .icon-system-info {
        background-image: url(../images/icon/icon-system-info.png);
      }
      .icon-bjtz {
        background-image: url(../images/icon/icon-bjtz.png);
      }
      .left {
        span {
          font-weight: 500;
          font-size: @30px;
          color: #333;
        }
      }
      .right {
        span {
          color: #999;
          font-size: @24px;
        }
      }
    }
    h3 {
      font-size: @28px;
      font-weight: 500;
      color: #333;
      padding-top: @30px;
      padding-bottom: @14px;
      line-height: 1;
    }
    .info {
      font-size: @24px;
      color: #999;
      line-height: @36px;
      padding-bottom: @20px;
    }
    .cell {
      height: @80px;
      border-top: 1px solid #ddd;
      .left {
        font-size: @24px;
        color: #333;
      }
    }
  }
  .ago {
    color: #999;
    text-align: center;
    font-size: @24px;
    padding: @10px 0 @24px 0;
    line-height: 1;
  }
}
