/*! 社保大事件 css */
.sb-dsj {
  padding: 0;
  min-height: 100%;
  background-color: #f1f7ff;
  .img-box {
    padding: 0;
    img {
      height: @376px;
    }
  }
  .time-line-wrapper {
    position: relative;
    padding-left: @66px;
    padding-top: @140px;
    padding-right: @30px;
    padding-bottom: @50px;
  }
  .time-line {
    padding-left: @54px;
    position: relative;
    &::before {
      position: absolute;
      content: "";
      width: @4px;
      height: 100%;
      top: -@92px;
      left: 0;
      background-color: #5c8ff4;
    }
  }
  .item {
    position: relative;
    margin-bottom: @132px;
    &:last-of-type {
      margin-bottom: 0;
      li:last-of-type {
        &::after {
          content: '';
          background-color: #f1f7ff;
          width: @20px;
          height: 50%;
          position: absolute;
          bottom: 0;
          left: -@60px;
          margin-top: -@8px;
        }
      }
    }
    .year {
      top: -@92px;
      left: -@86px;
      width: @338px;
      height: @68px;
      text-align: center;
      font-size: @30px;
      line-height: @54px;
      color: #fff;
      background: url(../images/zty/bg-03.png) no-repeat top left;
      background-size: 100%;
      padding-left: @64px;
      position: absolute;
      text-align: left;
      z-index: 99;
      &::before {
        content: "";
        position: absolute;
        top: @18px;
        left: @26px;
        width: @18px;
        height: @18px;
        border-radius: 50%;
        background-color: #fff;
      }
    }
    li {
      background-color: #fff;
      border-radius: @4px;
      padding: @20px @20px 0;
      margin-bottom: @30px;
      position: relative;
      .time {
        font-size: @32px;
        line-height: 1;
        font-weight: 500;
        color: #333;
        padding-bottom: @16px;
      }
      p {
        font-size: @28px;
        line-height: @36px;
        color: #333;
        span {
          color: #5c8ff4;
          &.orange {
            color: #ff8a00;
          }
        }
      }
      .tag {
        padding: @10px 0 @22px;
        span {
          font-size: @26px;
          color: #5c8ff4;
          background-color: #e1efff;
          border-radius: @4px;
          padding: @8px @20px;
          line-height: 1;
          margin-right: @10px;
          &.orange {
            background-color: #fff6e1;
            color: #daa518;
          }
        }
      }
      .line {
        position: absolute;
        top: 0;
        left: -@54px;
        width: @40px;
        height: auto;
        border-bottom: @2px dashed #5c8ff4;
        z-index: 999;
        top: 50%;
        transform: translateY(-50%);
        &::before {
          position: absolute;
          top: 50%;
          left: 0;
          width: @12px;
          height: @12px;
          content: "";
          border-radius: 50%;
          background-color: #5c8ff4;
          transform: translateY(-50%);
          z-index: 10000;
          margin-left: -@4px;
        }
      }
    }
  }
  .footer {
    height: @140px;
    line-height: 1;
    padding-top: @46px;
    font-weight: 500;
    font-size: @28px;
    color: #fff;
    text-align: center;
    background: url(../images/zty/bg-02.png) no-repeat top left;
    background-size: cover;
  }
}
