/*! 成功页面样式 css */
.success {
  padding: 0;
  .img-box {
    width: 100%;
    padding: @80px 0 @30px;
    img {
      width: @202px;
      height: @150px;
      margin: auto;
    }
  }
  .hint-info {
    text-align: center;
    p {
      font-size: @36px;
      line-height: 1;
      color: #333;
      font-weight: 500;
    }
    span {
      display: block;
      font-size: @28px;
      line-height: 1;
      color: #999;
      padding-top: @20px;
      padding-bottom: @16px;
    }
    b {
        font-weight: normal;
        color: #007cf7;
    }
  }
}
