/*! 办件进度查询 css */
.bjjdcx {
  padding: 0 @30px;
  ul {
    padding-top: @120px;
    padding-left: @186px;
  }
  li {
    position: relative;
    padding-left: @48px;
    border-left: @4px dashed #ddd;
    margin-bottom: @80px;
    .time {
      top: -@60px;
      left: -@186px;
      font-size: @28px;
      color: #999;
      position: absolute;
      line-height: 1;
      text-align: center;
      span {
        display: inline-block;
        font-size: @26px;
        color: #999;
        padding-top: @24px;
      }
    }
    .main {
      position: relative;
      top: -@60px;
    }
    .title {
      font-size: @34px;
      line-height: 1;
      position: relative;
      font-weight: 700;
      padding-bottom: @14px;
      color: #999;
      i {
        width: @40px;
        height: @40px;
        border: @4px solid #ddd;
        background-color: #fff;
        border-radius: 50%;
        position: absolute;
        top: 0;
        left: -@52px;
        transform: translateX(-50%);
      }
    }
    .info {
      font-size: @26px;
      line-height: @46px;
      color: #999;
    }
    &.ok {
      border-left: @4px solid #12d6e2;
      .time {
        color: #333;
      }
      .title {
        color: #333;
        i {
          border: 0;
          background: url(../images/icon/icon-ok.png) no-repeat center center;
          background-size: @80px @80px;
        }
      }
      .info {
        color: #666;
      }
    }
    &.error {
        border-left: @4px solid #f00;
        .time {
          color: #333;
        }
        .title {
          color: #f00;
          i {
            border: 0;
            background: url(../images/icon/icon-error.png) no-repeat center center;
            background-size: @80px @80px;
          }
        }
      }
    &:last-of-type {
      border-color: transparent;
    }
  }
}
