/*! 社保新鲜事 css */
.sbxxs {
  padding: 0;
  background: url(../images/zty/bg-01.png) no-repeat top left;
  background-size: 100% auto;
  .h-496 {
    height: @496px;
  }
  .img-box {
    padding: @16px 0;
    img {
      width: @234px;
      height: auto;
      margin: auto;
    }
  }
  .line {
    width: @426px;
    height: @4px;
    background: linear-gradient(
      to right,
      rgba(226, 225, 236, 0),
      rgba(226, 225, 236, 0.5),
      rgb(226, 225, 236),
      rgba(226, 225, 236, 0.5),
      rgba(226, 225, 236, 0)
    );
    margin: auto;
  }
  .item {
    font-size: @24px;
    line-height: @42px;
    text-align: center;
    padding-top: @40px;
    padding-bottom: @24px;
    .title {
      margin-bottom: @10px;
      span {
        display: inline-block;
        font-size: @24px;
        color: #fff;
        background-color: #e85853;
        border-radius: 20em;
        line-height: @44px;
        padding: 0 @16px;
      }
    }
    p {
      color: #000;
    }
    &.last {
      padding-top: @20px;
      font-size: @22px;
    }
  }
}
