/*! 材料寄送成功 css */
.cljscg {
  padding: 0;
  min-height: 100%;
  background-color: #fff;
  .top {
    padding: @36px @30px;
    div {
      font-size: @28px;
      line-height: 1;
      color: #333;
    }
    .first {
      padding-bottom: @20px;
    }
    p {
      color: #007cf7;
      font-size: @26px;
    }
    span {
      color: #999;
      font-size: @26px;
    }
  }
  .center {
    .hint {
      padding: 0 @30px;
      color: #666;
      font-size: @24px;
      span {
        display: block;
        line-height: @98px;
        border-bottom: 1px solid #ddd;
      }
      a {
        color: rgb(0, 127, 251);
      }
    }
  }
}
.lc-wrapper {
  padding: 0 @30px;
  position: relative;
  .lc {
    border-top: 1px dashed #ddd;
    &.success {
      padding-bottom: @112px;
    }
  }
  .shou {
    padding-left: @70px;
    padding-top: @40px;
    position: relative;
    .left {
      width: @68px;
      height: @68px;
      border-radius: 50%;
      background-color: #d5e1ec;
      line-height: @68px;
      color: #fff;
      text-align: center;
      margin-right: @30px;
    }
    .right {
      color: #333;
      font-size: @28px;
      line-height: 1;
      p:first-of-type {
        padding-bottom: @10px;
      }
    }
    &.line {
      position: relative;
      padding-bottom: @30px;
      &::before {
        content: "";
        position: absolute;
        top: @118px;
        left: @106px;
        width: @2px;
        height: @40px;
        background-color: #ddd;
      }
    }
    .time {
      position: absolute;
      left: 0;
      top: @40px;
      p {
        line-height: 1;
        font-size: @26px;
        color: #666;
        &:last-of-type {
          font-size: @22px;
          color: #999;
          padding-top: @6px;
        }
      }
    }
    &.success {
      padding-left: @90px;
      .left {
        background-color: #057ff7;
      }
    }
  }
  ul {
    // padding: 0 @30px 0 0;
    padding-top: @60px;
  }
  li {
    min-height: @164px;
    position: relative;
    padding-left: @170px;
    padding-bottom: @40px;
    margin-bottom: @30px;
    &:last-of-type {
      &::before {
        height: 0;
      }
    }
    &::before {
      content: "";
      width: @2px;
      height: 100%;
      position: absolute;
      top: 0;
      left: @106px;
      background-color: #ddd;
    }
    &.active {
      i {
        width: @68px;
        height: @68px;
        left: -@96px;
        top: 0;
        transform: translate(0, 0);
        background-color: #057ff7;
      }
      .time {
        top: -@30px;
      }
      &::before {
        height: @116px;
        top: auto;
        bottom: 0;
      }
      &:last-of-type {
        &::before {
          height: 0;
        }
      }
    }
    .time {
      position: absolute;
      font-size: @30px;
      line-height: 1;
      color: #999;
      top: -@38px;
      left: 0;
      p:first-of-type {
        padding-bottom: @10px;
      }
    }
    .content {
      display: block;
      font-size: @30px;
      line-height: 1;
      color: #999;
      position: relative;
      top: -@30px;
      p {
        position: relative;
        &:last-of-type {
          font-size: @28px;
          padding-top: @14px;
          line-height: @40px;
        }
      }
      a {
        color: rgb(5, 127, 247);
      }
    }
    i {
      position: absolute;
      top: 50%;
      left: -@68px;
      width: @10px;
      height: @10px;
      background-color: #aaaaaa;
      border-radius: 50%;
      transform: translateY(-50%);
    }
  }
  .see-more {
    position: absolute;
    left: 0;
    width: 100%;
    bottom: 0;
    font-size: @24px;
    color: #999;
    &.success {
      bottom: @30px;
    }
    i {
      color: #999;
      font-size: @26px;
      margin-left: @6px;
    }
  }
}
