/*! 关联账号绑定申请 css */
.glzhbdsq {
  padding: @30px;
  li {
    box-shadow: 0 0 @4px @4px #f7f8f9;
    height: @280px;
    margin-bottom: @30px;
    padding: @30px 0 0 @20px;
    border-radius: @6px;
    p {
      font-weight: 500;
      color: #333;
      font-size: @30px;
      line-height: 1;
      padding-bottom: @10px;
    }
    span {
      color: #999;
      font-size: @24px;
      line-height: @40px;
    }
    .btn-box {
      padding: 0 @30px 0 @10px;
      padding-top: @14px;
    }
    .btn {
      width: @304px;
      height: @44px;
      background-color: transparent;
      border: 1px solid #f70051;
      color: #f70051;
      line-height: @44px;
      font-size: @24px;
      &:first-of-type {
        border: 1px solid #1a90fe;
        color: #1a90fe;
      }
    }
    .time {
    }
  }
}
