/*! 身份认证 css */
.sfrz {
  padding: @40px @30px 0;
  min-height: 100%;
  background-color: #fff;
  .type {
    box-shadow: 0 0 @4px @4px #f7f8f9;
    margin-bottom: @30px;
  }
  .one {
    background: url(../images/bg/bg-rz.png) no-repeat top right;
    background-size: @690px @294px;
  }
  .hint {
    padding: @54px @50px @138px;
    color: #fff;
    font-size: @36px;
    line-height: @48px;
  }
  .icon {
    width: @32px;
    height: @32px;
    margin-right: @10px;
  }
  .icon-id-card {
    background-image: url(../images/icon/icon-id-card.png);
  }
  .icon-scan {
    background-image: url(../images/icon/icon-scan.png);
  }
  .wrapper {
    padding: @30px @30px @24px;
  }
  .title {
    font-size: @32px;
    color: #333;
    line-height: 1;
  }
  .main {
    padding-top: @30px;
    font-size: @44px;
    line-height: 1;
    color: #333;
    font-weight: 500;
    .btn {
      width: @170px;
      height: @60px;
      border-radius: 90em;
      font-size: @28px;
      line-height: @60px;
      box-shadow: 0 @8px @12px @4px rgba(0, 124, 247, 0.25);
    }
  }
  .tip {
    display: inline-block;
    font-size: @28px;
    color: #999;
    background-color: #f6f6f6;
    padding: @12px @16px;
    line-height: 1;
    border-radius: 20em;
    margin-top: @20px;
  }
}
