/*! 微信绑定 css */
.wechat-bind {
  padding: 0;
  min-height: 100%;
  .top {
    padding: @12px @30px 0;
  }
  .img-box {
    padding: @22px 0;
    img {
      width: @66px;
      height: @66px;
    }
  }
  p {
    font-size: @30px;
    color: #333;
    padding-left: @10px;
  }
  .icon-username {
    width: @30px;
    height: @34px;
    background-image: url(../images/icon/icon-user.png);
  }
  .icon-password {
    width: @28px;
    height: @34px;
    background-image: url(../images/icon/icon-password.png);
  }
  .input-wrapper {
    margin-top: @22px;
    .input-box {
      .left {
        width: @48px;
        min-width: auto;
      }
      .right {
        text-align: left;
      }
      input {
        width: 100%;
        text-align: left;
      }
    }
  }
  .link-wrapper {
    padding: @30px @30px @20px;
    .link {
      font-size: @28px;
      line-height: 1;
      color: #007cf7;
    }
  }
}
