/*! app 下载页 css */
.app-download {
  padding: 0;
  min-height: 100%;
  .top {
    padding-bottom: @56px;
    .img-box {
      padding: 0;
      img {
        width: 100%;
        height: @450px;
      }
    }
  }
  .QR {
    padding-bottom: @54px;
    .img-box {
      padding: 0;
      img {
        width: @360px;
        height: @350px;
        margin: auto;
      }
    }
  }
  .btn-box {
    padding: 0 @202px @20px;
    .btn {
      padding-left: @50px;
      margin-bottom: @20px;
      font-size: @28px;
      font-weight: normal;
      background-color: #318eea;
    }
    .icon-ios {
      width: @24px;
      height: @28px;
      margin-right: @8px;
      background-image: url(../images/icon/icon-ios.png);
    }
    .icon-android {
      width: @24px;
      height: @28px;
      margin-right: @8px;
      background-image: url(../images/icon/icon-android.png);
    }
  }
}
/*! 协议签名 css */
.xyqm {
  padding: 0;
  min-height: 100%;
  .rename-wrapper {
    padding: 0 @30px;
  }
  .rename {
    height: @320px;
    border: 1px solid #ddd;
    border-radius: @4px;
    text-align: center;
    line-height: @320px;
    font-size: @60px;
  }
}
