/*! 手机预约 css */
.sjyy {
  padding: 0;
  .btn-wrapper {
    padding: 0 @30px;
    .btn-box {
      border-bottom: 1px solid #ddd;
      padding: @44px 0;
    }
    .btn {
      width: @222px;
      height: @100px;
      background-color: #f6f5f6;
      color: #333;
      line-height: @100px;
      font-size: @32px;
      &.left {
        border-top-left-radius: 20em;
        border-bottom-left-radius: 20em;
      }
      &.right {
        border-top-right-radius: 20em;
        border-bottom-right-radius: 20em;
      }
      &.active {
        background-color: #057ff7;
        color: #fff;
      }
      .icon {
        width: @68px;
        height: @76px;
      }
      .icon-unit {
        background-image: url(../images/icon/icon-unit.png);
        margin-right: @20px;
      }
      .icon-person {
        background-image: url(../images/icon/icon-person.png);
        margin-left: @20px;
      }
    }
  }
}
