/*! 我的物流管理 css */
.my-wlgl {
  padding: 0;
  min-height: 100%;
  padding-bottom: @40px;
  .header {
    height: @320px;
    padding: 0;
    position: relative;
    background: url(../images/bg/bg.png) no-repeat top left;
    background-size: cover;
    .icon-address {
      position: absolute;
      width: @20px;
      height: @28px;
      top: @60px;
      left: @60px;
      background-image: url(../images/icon/icon-address-02.png);
    }
    .btn {
      background: linear-gradient(
        to right,
        rgb(247, 200, 13),
        rgb(250, 159, 1)
      );
      font-size: @28px;
      line-height: 1;
      padding: @12px;
      height: auto;
      width: auto;
      position: absolute;
      left: @60px;
      top: @190px;
    }
  }
  .tabs {
    position: static;
    padding: 0 @140px;
    border: 0;
  }
  .order-code-wrapper {
    padding: 0 @30px;
  }
  .order-code {
    height: @100px;
    border-top: 1px dashed #ddd;
    border-bottom: 1px solid #ddd;
    font-size: @28px;
    line-height: 1;
    color: #333;
    span {
      color: #999;
    }
  }
  .lc-wrapper {
    .lc {
      border: 0;
    }
  }
}
