/*! 智能咨询 css */
.wldzgl {
  padding: 0;
  ul {
    padding: 0 @30px;
  }
  li {
    border-bottom: 1px solid #ddd;
    height: @150px;
    .btn {
      width: @72px;
      height: @60px;
      line-height: @60px;
      background-color: transparent;
      font-size: @26px;
      color: #999;
      text-align: right;
      border-left: 1px solid #ddd;
    }
    p {
      color: #333;
      font-size: @28px;
      font-weight: 500;
      line-height: 1;
      padding-right: @10px;
    }
    .address {
      padding-top: @20px;
      p {
        font-weight: normal;
        font-size: @24px;
      }
      .default {
        color: #057ff7;
        font-size: @20px;
        line-height: @28px;
        padding: 0 @12px;
        background-color: #dfefff;
        border-radius: @2px;
        margin-right: @10px;
      }
    }
    span {
      color: #999;
      font-size: @26px;
      line-height: 1;
    }
  }
}
