/*! 亲情号选择 css */
.qqh-select {
  .before-line-box {
    span {
      line-height: @100px;
    }
  }
  li {
    .right {
      span {
        color: #333;
        position: relative;
        padding: 0 0 0 @16px;
        &::before {
          content: '';
          width: @8px;
          height: @8px;
          border-radius: 50%;
          background-color: #7be612;
          position: absolute;
          top: 50%;
          transform: translateY(-50%);
          left: 0;
        }
      }
    }
  }
}
.qqh-yz {
  .before-line-box {
    span {
      line-height: @100px;
    }
  }
  .btn-box {
      padding: @40px @60px;
  }
}
