/*! input.less */
input[type="checkbox"] {
  display: none;
}
.input-wrapper {
  padding: 0 @30px;
  .input-box {
    height: @100px;
    border-bottom: 1px solid #ddd;
    font-size: @28px;
  }
  .left {
    min-width: @200px;
    color: #333;
  }
  .right {
    text-align: center;
    span {
      color: #999;
      line-height: 1;
    }
    .icon-address {
      width: @20px;
      height: @26px;
      background-image: url(../images/icon/icon-address.png);
      margin-right: @10px;
    }
    .icon-arrow-right-grey-1424 {
      margin-left: @16px;
    }
  }
  input {
    font-size: @28px;
    line-height: 1.4;
    text-align: right;
    color: #999;
    &:checked {
      ~ .icon-switch {
        background-color: #007cf7;
        &::before {
          left: auto;
          right: @4px;
        }
      }
    }
  }
  select {
    font-size: @28px;
    line-height: 1.4;
    text-align: right;
    color: #999;
  }
}
.icon-switch {
  width: @76px;
  height: @40px;
  background-color: #ddd;
  border-radius: 99em;
  position: relative;
  transition: all 0.3s;
  &::before {
    content: "";
    position: absolute;
    top: 50%;
    left: @4px;
    background-color: #fff;
    width: @34px;
    height: @34px;
    border-radius: 50%;
    transform: translateY(-50%);
    transition: all 0.3s;
  }
}
