/*! 弹窗 css */
.popup {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  background-color: transparent;
  z-index: 998;
  .content-wrapper {
    width: 100%;
    position: absolute;
    top: 50%;
    left: 0;
    z-index: 999;
    border-radius: 0;
    background-color: transparent;
    transform: translate(0, -50%);
    &.bottom {
      bottom: 0;
      top: auto;
    }
  }
  .mask {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    z-index: 998;
    background-color: rgba(0, 0, 0, 0.7);
  }
}
.popup.s-v-m {
  .content-wrapper {
    padding: 0 @30px;
    transform: translate(0, 0);
  }
  .content {
    position: static;
    width: 100%;
    height: auto;
    padding: 0 0 @40px 0;
    background-color: transparent;
    border-radius: 0;
    transform: translate(0, 0);
    .btn {
      background-color: #fff;
      color: #999;
      font-size: @36px;
      border-radius: @4px;
      line-height: @100px;
      height: @100px;
    }
    .nav {
      background-color: #fff;
      margin-bottom: @20px;
      border-top-left-radius: @10px;
      border-top-right-radius: @10px;
      color: #333;
      text-align: center;
      padding: 0 @30px;
    }
    h3 {
      color: #007cf7;
      font-weight: 500;
      font-size: @28px;
      height: @110px;
      line-height: @110px;
    }
    li {
      font-size: @28px;
      height: @110px;
      line-height: @110px;
      border-top: 1px solid #ddd;
    }
  }
}
