/*! 社保大数据 css */
.sbdsj {
  padding: 0;
  min-height: 100%;
  background: url(../images/sbdsj/bg-01.png) no-repeat top left
    rgb(47, 143, 252);
  background-size: 100% auto;
  position: relative;
  .h-668 {
    height: @668px;
  }
  .tip-wrapper {
    padding: 0 @40px;
    margin-bottom: @56px;
    .tip {
      border: @6px solid #41b6fa;
      padding: @13px @44px;
      font-size: @30px;
      color: #fff;
      line-height: @56px;
      b {
        font-weight: normal;
        color: rgb(255, 237, 133);
      }
    }
  }
  .remark {
    padding: 0 @36px;
    background: url(../images/sbdsj/bg-02.png) no-repeat bottom left;
    background-size: 100% @1043px;
    .bg {
      height: @322px;
      position: relative;
      background: url(../images/sbdsj/bg-03.png) no-repeat top left;
      background-size: 100% @322px;
      margin-bottom: @56px;
      .img-box {
        padding: 0;
        img {
          width: auto;
          height: @40px;
          margin: auto;
        }
      }
    }
    .main {
      margin-top: @74px;
      padding-left: @36px;
      .icon {
        width: @112px;
        height: @112px;
        margin-right: @20px;
      }
      .icon-sb {
        background-image: url(../images/sbdsj/logo-03.png);
      }
      .icon-ie {
        background-image: url(../images/sbdsj/logo-04.png);
      }
      .icon-dt {
        background-image: url(../images/sbdsj/logo-02.png);
      }
      .icon-all {
        background-image: url(../images/sbdsj/logo-01.png);
      }
      .right {
        color: #fff;
        font-weight: 500;
        font-size: @32px;
        line-height: 1;
        b {
          font-weight: 700;
          font-size: @44px;
          color: rgb(255, 220, 54);
        }
      }
    }
  }
  .footer {
    font-size: @26px;
    line-height: 1;
    color: #fff;
    text-align: center;
    padding: @20px 0 @60px;
  }
}
