/*! 智能咨询 css */
.lqsbk {
  padding: 0;
  min-height: 100%;
  .img-box {
    padding-top: @60px;
    padding-bottom: @36px;
    img {
      width: @420px;
      height: @260px;
      margin: auto;
    }
  }
  .hint-info {
    color: #999;
    font-size: @30px;
    line-height: @42px;
    padding: 0 @60px;
    text-align: center;
    padding-bottom: @74px;
    .link {
      color: rgb(0, 124, 247);
    }
  }
  .cell-wrapper {
    padding: 0 @30px;
  }
  .cell {
    height: @100px;
    border-top: 1px dashed #ddd;
    border-bottom: 1px solid #ddd;
    font-size: @28px;
    color: #333;
    .right {
      color: #999;
    }
  }
  .btn-box {
    text-align: center;
    .link {
      display: inline-block;
      font-size: @28px;
      color: #007cf7;
      margin: @60px 0;
    }
  }
  .password-wrapper {
    text-align: center;
    padding-top: @200px;
    h3 {
      font-size: @36px;
      font-weight: 500;
      color: #333;
      line-height: 1;
      padding-bottom: @60px;
    }
    p {
      color: #999;
      font-size: @28px;
      line-height: 1;
      padding-bottom: @50px;
    }
    .password-box {
      padding: 0 @140px;
      input {
        border-top: 1px solid #ddd;
        border-right: 1px solid #ddd;
        border-bottom: 1px solid #ddd;
        text-align: center;
        width: @80px;
        height: @96px;
        line-height: 1.4;
        &:first-of-type {
          border-left: 1px solid #ddd;
        }
      }
    }
    #rel_input {
        // display: none;
        // visibility: hidden;
        position: fixed;
        top: -200%;
        left: -200%;
    }
  }
}
